@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom theme tokens */
:root {
	--grad-start: #A770EF; /* purple */
	--grad-mid: #CF8BF3;   /* pink */
	--grad-end: #FDB99B;   /* peach */
	--border-gainsboro: gainsboro; /* #DCDCDC */
}

@layer base {
	html, body, #root {
		height: 100%;
	}
	body {
		@apply antialiased text-gray-800;
		background: linear-gradient(120deg, rgba(167,112,239,0.08), rgba(207,139,243,0.08) 50%, rgba(253,185,155,0.08));
	}
}

@layer utilities {
	.bg-grad {
		background: linear-gradient(90deg, var(--grad-start), var(--grad-mid), var(--grad-end));
	}
	.text-grad {
		background: linear-gradient(90deg, var(--grad-start), var(--grad-mid), var(--grad-end));
		-webkit-background-clip: text;
		background-clip: text;
		color: transparent;
	}
	.border-gainsboro { border-color: var(--border-gainsboro) !important; }
	.rounded-12 { border-radius: 12px; }
}

@layer components {
	.card {
		@apply bg-white/70 backdrop-blur border border-gainsboro rounded-12 p-4 shadow-sm;
	}
	.btn {
		@apply inline-flex items-center justify-center px-5 py-2.5 rounded-full text-white font-medium transition-colors shadow-sm;
		background: linear-gradient(90deg, var(--grad-start), var(--grad-mid), var(--grad-end));
	}
	.btn:hover { filter: brightness(0.98); }
	.btn:disabled { @apply opacity-60 cursor-not-allowed; }

	.btn-outline {
		@apply inline-flex items-center justify-center px-5 py-2.5 rounded-full font-medium transition-colors text-gray-800 border border-gainsboro bg-white;
	}
	.btn-outline:hover { @apply bg-gray-50; }

	.input {
		@apply w-full px-3 py-2 border border-gainsboro rounded-12 bg-white/80 focus:outline-none focus:ring-2 focus:ring-purple-300;
	}

	.badge {
		@apply inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium border border-gainsboro;
		background: #fff;
	}
	.badge-purple { background: rgba(167,112,239,0.15); color: #5a2db4; }
	.badge-pink { background: rgba(207,139,243,0.15); color: #7e3aae; }
	.badge-peach { background: rgba(253,185,155,0.18); color: #b45a2d; }

	.tabs {
		@apply flex gap-2 p-1 rounded-full border border-gainsboro bg-white/70 backdrop-blur w-full overflow-x-auto;
	}
			.tab {
				@apply px-4 py-2 rounded-full text-sm font-medium;
				color: #5a2db4;
				background: #fff;
                /* border: 2px solid transparent; */

			}
			.tab:hover {
				color: #fff;
				background: var(--grad-start);
                /* border: 2px solid transparent; */
			}
			.tab-active {
				color: #fff;
				background: var(--grad-start);
				/* border: 2px solid var(--grad-start); */
			}

	.section-title { @apply text-lg font-semibold mb-3; }

	/* Simple stacked progress bar for votes */
	.vote-bar {
		display: flex;
		height: 10px;
		border: 1px solid var(--border-gainsboro);
		border-radius: 12px;
		overflow: hidden;
		background: #fff;
	}
	.vote-for {
		background: var(--grad-start);
	}
	.vote-against {
		background: var(--grad-end);
	}
}

/* Toast styles for status cards */
/* Toast styles with main app gradient */
/* Toast styles with status colors and gradient shades */
.toast {
	min-width: 220px;
	max-width: 350px;
	padding: 0.75rem 1.25rem;
	border-radius: 0.75rem;
	font-size: 1rem;
	font-weight: 500;
	box-shadow: 0 2px 12px rgba(0,0,0,0.10);
	margin-bottom: 0.5rem;
	color: #fff;
	animation: fadein 0.3s, fadeout 0.5s 3s;
}
.toast-success {
	border-left: 6px solid #22c55e;
	background: linear-gradient(90deg, #22c55e 0%, #16a34a 100%);
}
.toast-error, .toast-danger {
	border-left: 6px solid #ef4444;
	background: linear-gradient(90deg, #ef4444 0%, #b91c1c 100%);
}
.toast-info {
	border-left: 6px solid #3b82f6;
	background: linear-gradient(90deg, #3b82f6 0%, #1e40af 100%);
}
.toast-warning {
	border-left: 6px solid #eab308;
	background: linear-gradient(90deg, #fde047 0%, #eab308 100%);
	color: #3b2f08;
}
@keyframes fadein {
	from { opacity: 0; transform: translateY(-10px); }
	to { opacity: 1; transform: translateY(0); }
}
@keyframes fadeout {
	from { opacity: 1; }
	to { opacity: 0; }
}